.card {
  width: 200px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 0;

  &__img {
    display: block;
    height: 134px;
    width: 160px;
    margin: 0 auto 40px;
  }

  &__text {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $color-text;
    margin-bottom: 4px;
  }

  &__product-code {
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    margin-bottom: 16px;
  }

  &__stars-reviews {
    display: flex;
    margin-bottom: 24px;
    justify-content: space-between;
    align-items: center;
  }

  &__reviews {
    color: $color-text;
    font-size: 10px;
    line-height: 14px;
  }

  &__price {
    display: flex;
    margin-bottom: 16px;
    justify-content: space-between;
  }

  &__item {
    font-size: 12px;
    line-height: 18px;
    color: #616070;
  }

  &__number {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $color-text;
  }

  &__buy {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    margin-bottom: 16px;
  }

  &__buy--decoration {
    border-radius: 5px;
    background: $color-button-links;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    text-decoration: none;
    padding: 12px;
    transition: all 0.3s ease-in-out;
  }

  &__buy:hover {
    background: #fff;
    border: 1px solid transparent;
    border-color: $color-button-links;
    color: $color-button-links;
  }
}
